body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.header{
  background-color: black;
  color: white;
  padding: 20px 50px;
  font-weight: bold;
  font-size: large;
  display: flex;
  justify-content: space-between;
}
.cart-icon .counter{
  height: 18px;
  width: 18px;
  background-color: blue;
  border-radius: 50%;
  position: relative;
  right: 7px;
  top: -6px;

}
.cart-icon{
  display: flex;
}
.icon{
  right:2px;
  font-size: 22px;
}
img{
  height: 150px;

}
.cart-wrapper{
display: inline-flex;
margin: 40px;
width: 320px;
box-shadow: 0px 0px 7px black;
border-radius: 5px;

}
.image-wrapper{
  width: 180px;
  display: flex;
  justify-content: center;
  padding: 10px;
  border-right: 1px solid rgb(201,201,201,);
}
.content{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
 padding: 10px;
}
.price{
  margin-bottom:auto ;
}
.content button{
  background-color: black;
  color: white;
  border-radius: 3px;
}
.quantity{
  display: flex;
  align-self: flex-end;
  border-radius: 3px;
  border: 1px solid grey;
  
}
.quantity div{
  height: 22px;
   width: 22px;
   display: flex;
   align-items: center;
   justify-content: center;
}
.quantity > div:first-child, 
.quantity > button {
  background-color: rgb(0, 0, 111) ;
  color: white;
  border-radius:0;
  

}
.quantity > button:disabled{
  opacity: 0.7;

}
main{
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  min-height: 80vh;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap: 50px;
  
}
.total{
  box-sizing: border-box;
  position: fixed;
  background-color: yellowgreen;
  padding:17px 40px;
  position: fixed;
  width: 100%;
  text-align: right;
  bottom: 0;
}
